@function color($n) {
    $colors: (
        'black': #0B0A0C,
        'white': #FCFCFD,
        'lighter': #1A191B,
    );
    @return map-get($colors, $n);
}

@function resource($f, $l, $e) {
    @return "../#{$l}/#{$f}.#{$e}";
}

@function image($f, $e: 'png') {
    @return resource($f, 'imgs', $e);
}


@mixin fontFace($n, $w, $f, $e, $i: false) {
    @font-face {
        font-family: $n;
        src: url(resource($f, 'fonts', $e));
        font-weight: $w;
        @if($i) {
            font-style: italic;
        } @else {
            font-style: normal;
        }
    }
}

@include fontFace('Lato', 300, 'Lato-Light', 'ttf');
@include fontFace('Lato', 400, 'Lato-Regular', 'ttf');
@include fontFace('Lato', 700, 'Lato-Bold', 'ttf');

body {
    background-color: color('black');
    color: color('white');
    font-family: 'Lato', sans-serif;
    margin: 0;
}

* {
    box-sizing: border-box;
    transition:  all .3s ease-in-out
}

#app {
    width: calc(100% - 500px);
    margin: 0 auto;
    padding: 0 28px;
    display: flex;
    flex-direction: column;

    header {
        display: flex;
        align-items: center;
        height: 115px;

        .avatar {
            background-image: url(image('photo', 'jpg'));
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            border-radius: 28px;
            width: 56px;
            height: 56px;
        }

        .info {
            display: flex;
            flex-direction: column;
            margin-left: 28px;

            span {
                font-size: 28px;
            }

            small {
                font-size: 14px;
                font-weight: 300;
                transform: translateY(-8px);
            }
        }
    }
    
    main {
        flex: 1;
        font-size: 130px;
        line-height: 100px;
        font-weight: 300;
        padding-top: 30px;
    }

    .guideLines {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;

        & > div {
            position: absolute;
            background-color: color('lighter');
        }

        & > .vertical {
            top: 0;
            width: 1px;
            height: 100%;
        }

        & > .horizontal {
            left: 0;
            height: 1px;
            width: 100%;
        }

        & > .left {
            left: 250px;
        }

        & > .right {
            right: 250px;
        }

        & > .top {
            top: 115px;
        }

        & > .bottom {
            bottom: 115px;
        }
    }
}

@media screen and (max-width: 1280px) {

    #app {
        width: calc(100% - 280px);

        main {
            font-size: 110px;
            line-height: 100px;
        }
        
        .guideLines {

            & > .left {
                left: 140px;
            }
    
            & > .right {
                right: 140px;
            }
        }
    }

}

@media screen and (max-width: 970px) {

    #app {
        width: calc(100% - 140px);

        header {

            height: 110px;
        }

        main {
            font-size: 90px;
            line-height: 80px;
        }
        
        .guideLines {

            & > .left {
                left: 70px;
            }
    
            & > .right {
                right: 70px;
            }
    
            & > .top {
                top: 110px;
            }
    
            & > .bottom {
                bottom: 110px;
            }
        }
    }

}

@media screen and (max-width: 600px) {

    #app {
        width: calc(100% - 110px);
        padding: 0 12px;

        header {
            height: 70px;

            .avatar {
                width: 42px;
                height: 42px;
            }
    
            .info {
                margin-left: 12px;

                span {
                    font-size: 22px;
                }
    
                small {
                    font-size: 14px;
                }
            }
        }

        main {
            font-size: 70px;
            line-height: 60px;
        }
        
        .guideLines {

            & > .left {
                left: 55px;
            }
    
            & > .right {
                right: 55px;
            }
    
            & > .top {
                top: 70px;
            }
    
            & > .bottom {
                bottom: 70px;
            }
        }
    }

}

@media screen and (max-width: 460px) {

    #app {
        width: calc(100% - 50px);
        
        main {
            font-size: 50px;
            line-height: 40px;
        }

        .guideLines {

            & > .left {
                left: 25px;
            }
    
            & > .right {
                right: 25px;
            }
        }
    }

}